#initialScreen {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.initialScreen-move-2-left {
  -webkit-animation: none 0s ease 0s 1 normal none runningScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
          animation: none 0s ease 0s 1 normal none runningScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
  -webkit-animation: initialScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
          animation: initialScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}

@-webkit-keyframes initialScreen-move-2-left {
  from { margin-left: 0; opacity: 1; display: block }
  to { margin-left: -1200px; opacity: 0; display: none }
}

@keyframes initialScreen-move-2-left {
  from { margin-left: 0; opacity: 1; display: block }
  to { margin-left: -1200px; opacity: 0; display: none }
}

.initialScreen-move-2-right {
  -webkit-animation: none 0s ease 0s 1 normal none runningScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
          animation: none 0s ease 0s 1 normal none runningScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
  -webkit-animation: initialScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
          animation: initialScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}


@-webkit-keyframes initialScreen-move-2-right {
  from { margin-left: -1200px; opacity: 0; display: none }
  to { margin-left: 0; opacity: 1; display: block }
}


@keyframes initialScreen-move-2-right {
  from { margin-left: -1200px; opacity: 0; display: none }
  to { margin-left: 0; opacity: 1; display: block }
}

