.use-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 70px;
    /* выравнивание по вертикали, хак. должно быть эквивалентно height элемента */
    /* line-height:70px; */
    color: #333;
    background: rgb(221, 221, 221);
    font-weight: 700;
    font-size: 2em;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
    padding: 10px 0 10px 0;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    -webkit-animation: hoverEffectReverse .3s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
            animation: hoverEffectReverse .3s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
    /* animation: hoverEffect .3s cubic-bezier(0.075, 0.82, 0.165, 1) 1 reverse; */
}

.use-button :only-child {
    pointer-events:none;
}

.use-button.active, .use-button.active.inprocess {
    color: white;
    text-shadow: 0 0 3px black;
}

.use-button.active {background-color: #18961A}

.use-button.active.inprocess {background-color: goldenrod}

.use-button-status {
    font-size: 12px;
    color: #E7F513;
}

.use-button:hover {
    -webkit-animation: hoverEffect .3s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
            animation: hoverEffect .3s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}
  
@-webkit-keyframes hoverEffect {
    /* from { transform: rotate(0deg); }
    to { transform: rotate(360deg); } */
    from {-webkit-transform: scale(1,1);transform: scale(1,1)}
    to {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1)}
}
  
@keyframes hoverEffect {
    /* from { transform: rotate(0deg); }
    to { transform: rotate(360deg); } */
    from {-webkit-transform: scale(1,1);transform: scale(1,1)}
    to {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1)}
}

@-webkit-keyframes hoverEffectReverse {
    /* from { transform: rotate(-90deg)};
    to { transform: rotate(90deg)}; */
    from {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1)}
    to {-webkit-transform: scale(1,1);transform: scale(1,1)}
}

@keyframes hoverEffectReverse {
    /* from { transform: rotate(-90deg)};
    to { transform: rotate(90deg)}; */
    from {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1)}
    to {-webkit-transform: scale(1,1);transform: scale(1,1)}
}

