#webScreen {
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    /* height: 100vh; */

    margin-left: 1500px;
    opacity: 0;
    display: none;
  
    /* width: 100%; */
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 50px;
}

#webScreen a {
    color: white;
    text-decoration: none;
}

#webScreen-header, #webScreen-session {
    font-size: 3rem;
}

#webScreen-session span {
    font-size: 7rem;
}

#webScreen-session {
    margin: 100px 0 200px 0;
}

#webScreen-button-container {
    display: flex;
    flex-direction: row;
}

.webScreen-move-2-left {
    display: block !important;
    -webkit-animation: webScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
            animation: webScreen-move-2-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}
  
.webScreen-move-2-right {
    -webkit-animation: webScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
            animation: webScreen-move-2-right 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}

.webScreen-move-2-left-out {
    display: block !important;
    -webkit-animation: webScreen-move-2-left-out 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
            animation: webScreen-move-2-left-out 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}
  
@-webkit-keyframes webScreen-move-2-left {
    from { margin-left: 1500px; opacity: 0 }
    to { margin-left: 150px; opacity: 1 }
}
  
@keyframes webScreen-move-2-left {
    from { margin-left: 1500px; opacity: 0 }
    to { margin-left: 150px; opacity: 1 }
}
  
@-webkit-keyframes webScreen-move-2-right {
    from { margin-left: 150px; opacity: 1 }
    to { margin-left: 1500px; opacity: 0 }
}
  
@keyframes webScreen-move-2-right {
    from { margin-left: 150px; opacity: 1 }
    to { margin-left: 1500px; opacity: 0 }
}

@-webkit-keyframes webScreen-move-2-left-out {
    from { margin-left: 150px; opacity: 1 }
    to { margin-left: -1500px; opacity: 0 }
}

@keyframes webScreen-move-2-left-out {
    from { margin-left: 150px; opacity: 1 }
    to { margin-left: -1500px; opacity: 0 }
}
#webscreen-back-button, #webscreen-start-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 210px;
    height: 70px;
    /* ������������ �� ���������, ���. ������ ���� ������������ height �������� */
    /* line-height:70px; */
    color: #333;
    background: rgb(221, 221, 221);
    font-weight: 700;
    font-size: 2em;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    text-transform: capitalize;
    margin: 12px auto 0 auto;
}

